<template>
  <div class="sector-container" v-if="setPermission">
    <Sector />
  </div>
</template>

<script>
import Sector from "../../components/MuniEnLinea/SectorComponent.vue";

export default {
  name: "SectorView",
  components: {
    Sector,
  },
  computed: {
    setPermission() {
      if (this.$store.state.loggedIn === true) {
        return true;
      } else {
        return false;
      }
    },
  },
};
</script>

<style scoped>
.sector-container {
  width: 100%;
  height: 100vh;
  display: flex;
  background-color: #f5f5f5;
}
</style>
